<template>
  <!--myIcon-->
  <div class="myIcon">
    <!--Icon 001-->
    <ul class="icon-01">
      <li class="icon-02" v-for="i in icon">
        <a class="icon-03" href="javascript:;">
          <img class="icon-04" :src="i.iconImg"/>
          <span class="icon-05">{{i.iconSpan}}</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "MyIcon",
      data() {
        return {
          icon: [
            {iconSpan: "",iconImg:"../../static/images/my-icon-01.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-02.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-03.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-04.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-05.png"},
            {iconSpan: "",iconImg:"../../static/images//my-icon-06.png"},
            {iconSpan: "/",iconImg:"../../static/images/my-icon-07.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-08.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-09.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-10.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-11.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-12.png"},
            {iconSpan: "",iconImg:"../../static/images/my-icon-13.png"},
          ]
        }

      }
    }
</script>

<style scoped>
    @import "../../../static/css/my.css";
</style>



